//internal
import BaseLayout from "../Layout/BaseLayout"
import TopBar from "../Components/TopBar";

//nextui
import { Image } from "@nextui-org/react";

// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/pagination';

// import required modules
import { Pagination } from 'swiper/modules';



function AboutText({title,contet,align,image}){
  return(
    <section dir={align} className={`grid grid-cols-1 lg:grid-cols-2 BaseContainer`}>
      <div dir="rtl" className="flex flex-col gap-2 items-start">
        <h2 className="text-MainColor-50 font-MorabbaMedium text-3xl">{title}</h2>
        <p className="font-PinarMedium text-justify leading-8">{contet}</p>
      </div>
      <div className="w-full flex justify-end">
        <img src={image} className="mx-auto lg:mx-0 ltr:lg:ml-auto rtl:lg:mr-auto" alt="درباره تیم ما" />
      </div>
    </section>
  );
}

function AboutGallery(){
  const images = [
    { id : 4, src:"assets/images/About/4.webp"},
    { id : 1, src:"assets/images/About/1.webp"},
    { id : 3, src:"assets/images/About/3.webp"},
    { id : 2, src:"assets/images/About/2.webp"},
  ];
  return(
    <section className="grid grid-cols-2 md:grid-cols-3 gap-4 BaseContainer">
      <img className="AboutImages" src="assets/images/About/4.webp" alt="معین وب" />
      <img className=" AboutImages md:col-span-2" src="assets/images/About/1.webp" alt="معین وب"  />
      <img className="AboutImages md:col-span-2" src="assets/images/About/3.webp" alt="معین وب" />
      <img className="AboutImages" src="assets/images/About/2.webp" alt="معین وب" />
    </section>
  );
}

function OurTeam(){
  const images = [
    { id:1 , image:"assets/images/Ourteam/1.webp"},
    { id:2 , image:"assets/images/Ourteam/2.webp"},
    { id:3 , image:"assets/images/Ourteam/3.webp"},
    { id:4 , image:"assets/images/Ourteam/4.webp"},
    { id:5 , image:"assets/images/Ourteam/5.webp"},
    { id:6 , image:"assets/images/Ourteam/6.webp"},
    { id:7 , image:"assets/images/Ourteam/7.webp"},
    { id:8 , image:"assets/images/Ourteam/8.webp"},
  ];
  return(
    <section className="BaseContainer space-y-4">
      <h3 className="text-MainColor-50 font-MorabbaMedium text-3xl">اعضای تیم ما</h3>
      <Swiper
        breakpoints={{
          300 : {slidesPerView : 1},
          576 : {slidesPerView : 2},
          1200 : {slidesPerView : 3},
          1600 : {slidesPerView : 4},
        }}
        loop={true}
        pagination={{
          dynamicBullets: true,
        }}
        spaceBetween={24}
        modules={[Pagination]}
        className="AboutUsSwiper"
      >
        {images.map(image=> 
        <SwiperSlide>
          <Image
            key={image.id}
            src={image.image} 
            className="hover:!opacity-90 border-2 border-black/5 mb-4"
          />
        </SwiperSlide> )
        }
      </Swiper>
    </section>
  );
}

const AboutUsPage = () => {

  
  return(
    <BaseLayout>
      <TopBar title="درباره ما" />
      <AboutText
      title="درباره تیم معین وب"
      contet="لورم ایپسوم متن ساختگی با تولید سادگی نامفهوم از صنعت چاپ، و با استفاده از طراحان گرافیک است، چاپگرها و متون بلکه روزنامه و مجله در ستون و سطرآنچنان که لازم است، و برای شرایط فعلی تکنولوژی مورد نیاز، و کاربردهای متنوع با هدف بهبود ابزارهای کاربردی می باشد، کتابهای زیادی در شصت و سه درصد گذشته حال و آینده، شناخت فراوان جامعه و متخصصان را می طلبد، تا با نرم افزارها شناخت بیشتری را برای طراحان رایانه ای علی الخصوص طراحان خلاقی، و فرهنگ پیشرو در زبان فارسی ایجاد کرد، در این صورت می توان امید داشت که تمام و دشواری موجود در ارائه راهکارها، و شرایط سخت تایپ به پایان رسد و زمان مورد نیاز شامل حروفچینی دستاوردهای اصلی، و جوابگوی سوالات پیوسته اهل دنیای موجود طراحی اساسا مورد استفاده قرار گیرد."
      image="assets/images/vectors/AboutTeam.svg"
      />
      <AboutGallery />
      <AboutText
      align="ltr"
      title="اهداف ما"
      contet="لورم ایپسوم متن ساختگی با تولید سادگی نامفهوم از صنعت چاپ، و با استفاده از طراحان گرافیک است، چاپگرها و متون بلکه روزنامه و مجله در ستون و سطرآنچنان که لازم است، و برای شرایط فعلی تکنولوژی مورد نیاز، و کاربردهای متنوع با هدف بهبود ابزارهای کاربردی می باشد، کتابهای زیادی در شصت و سه درصد گذشته حال و آینده، شناخت فراوان جامعه و متخصصان را می طلبد، تا با نرم افزارها شناخت بیشتری را برای طراحان رایانه ای علی الخصوص طراحان خلاقی، و فرهنگ پیشرو در زبان فارسی ایجاد کرد، در این صورت می توان امید داشت که تمام و دشواری موجود در ارائه راهکارها، و شرایط سخت تایپ به پایان رسد و زمان مورد نیاز شامل حروفچینی دستاوردهای اصلی، و جوابگوی سوالات پیوسته اهل دنیای موجود طراحی اساسا مورد استفاده قرار گیرد."
      image="assets/images/vectors/Goal.svg"
      />
      <OurTeam />
    </BaseLayout>
  );
}


export default AboutUsPage;